.sub-header {
  width: 100%;
  border-bottom: 1px solid #306;
  background: #eee;
  box-shadow: 1px 1px 7px rgba(0,0,0,0.3);

  @media only screen and (min-width: 700px) {
    position: fixed;
    top: 100px;
    z-index: 1400;
  }
}

.sub-nav {
  position: relative;
  list-style: none;
  margin: 0;
  padding: 0;
  text-align: left;
  list-style: none;
  text-transform: lowercase;
}

.sub-nav > li {
  float: left;
  font-weight: 400;
  font: 16px 'Raleway', sans-serif;

  @media only screen and (min-width: 700px) {
    font-size: 18px
  }
}

.sub-nav a {
  padding: 5px;
  display: block;
  color: #306;
  text-decoration: none;

  @media only screen and (min-width: 700px) {
    padding: 10px 14px;
  }
}

.sub-nav a:hover,
.sub-nav a:active,
.sub-nav a:focus {
  color: #ff009a;
}

.sub-nav .selected a {
  color: #ff009a;
  background: url(../img/selected.gif) center bottom no-repeat;
}

// Hidden so nav highlighting works as expected
.sub-nav-home {
  display: none;
}

.has-dropdown {
  position: relative;
}

.dropdown {
  display: none;
  position: absolute;
  width: 200px;
  left: 50%;
  top: 100%;
  margin-left: -100px;
  border: 1px solid $primary-color;
  background: #eee;
  border-radius: 5px;
  padding: 0.75rem 0;
}

.dropdown:after {
  bottom: 100%;
  left: 50%;
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
  border-bottom-color: $primary-color;
  border-width: 10px;
  margin-left: -10px;
}

.dropdown ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.selected .dropdown a,
.dropdown .selected a {
  background: none;
}

.has-dropdown:hover .dropdown,
.has-dropdown:active .dropdown,
.has-dropdown:focus .dropdown {
  display: block;
}

.hide-dropdown,
.has-dropdown:hover .hide-dropdown,
.has-dropdown:active .hide-dropdown,
.has-dropdown:focus .hide-dropdown {
  display: none;
}
